




















.navbar nav{
    padding: 0 20px;
}

.index{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin-top: 60px;
}
.index h2{
  font-size: 1.8em;
  text-align: center;
  margin-top: 0;
}
.index .items{
  margin: 30px auto;
}
.index .items li{
  display: inline-block;
}
.index .delete{
  position: absolute;
  top: 4px;
  right: 4px;
  cursor: pointer;
  color: #aaa;
  font-size: 1.4em;
}

.add-list {
  margin-top: 60px;
  padding: 20px;
  max-width: 500px;
}
.add-list h2 {
  font-size: 2em;
  margin: 20px auto;
}
.add-list .field {
  margin: 20px auto;
  position: relative;
}
.add-list .delete {
  position: absolute;
  right: 0;
  bottom: 16px;
  color: #aaa;
  font-size: 1.4em;
  cursor: pointer;
}

.edit-list{
     margin-top: 60px;
     padding: 20px;
     max-width: 500px;
}
.edit-list h2{
     font-size: 2em;
     margin: 20px auto;
}
.edit-list .field{
     margin: 20px auto;
     position: relative;
}
.edit-list .delete{
     position: absolute;
     right: 0;
     bottom: 16px;
     color: #aaa;
     font-size: 1.4em;
     cursor: pointer;
}
